.example {
    margin: 0 auto;
    background-color: $grey-ea;
    @media (min-width: $tablet-width) {
        background-color: transparent;
    }
    @media (min-width: $desktop-width) {
        background-color: $grey-f2;
        margin-top: 206px;
    }
}

.example__wrap {
    @media (min-width: $desktop-width) {
        display: flex;
        width: 1220px;
        margin: 0 auto;
    }
}

.example__wrapper {
    display: flex;
    flex-direction: column;
    width: 280px;
    margin: 0 auto;
    @media (min-width: $tablet-width) {
        width: 708px;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    @media (min-width: $desktop-width) {
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        width: 436px;
        padding: 0;
        margin-right: 134px;
        margin-bottom: -136px;
        position: relative;
        top: -136px;
    }
}

.example__headline {
    font-style: normal;
    font-weight: normal;
    text-transform: none;
    font-size: 36px;
    line-height: 40px;
    margin-top: 27px;
    @media (min-width: $tablet-width) {
        font-size: 60px;
        line-height: 60px;
        margin-top: 43px;
    }
    @media (min-width: $desktop-width) {
        margin: 0;
        margin-bottom: 146px;
    }
}

.example__text {
    font-family: $arial;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 18px;
    text-transform: none;
    color: $grey;
    margin-bottom: 25px;
    margin-top: 33px;
    width: 279px;
    @media (min-width: $tablet-width) {
        font-size: 16px;
        line-height: 24px;
        margin-top: 63px;
        margin-bottom: 54px;
        width: auto;
    }
    @media (min-width: $desktop-width) {
        width: 436px;
        margin: 0;
        margin-bottom: 69px;
    }
}

.example__statistic {
    display: flex;
    justify-content: space-between;
    width: 280px;
    margin: 0;
    margin-bottom: 34px;
    @media (min-width: $tablet-width) {
        width: 366px;
        margin-bottom: 59px;
    }
    @media (min-width: $desktop-width) {
        margin-bottom: 60px;
    }
}

.example__costs {
    font-size: 14px;
    line-height: 20px;
    margin: 0 auto;
    @media (min-width: $tablet-width) {
        font-size: 20px;
        line-height: 20px;
        width: 174px;
        text-align: right;
        margin: 0;
        padding-top: 10px;
        & span {
            display: block;
            margin-bottom: 9px;
        }
    }
    @media (min-width: $desktop-width) {
        display: flex;
        justify-content: space-between;
        width: 327px;
        min-height: 0;
        padding: 0;
    }
}

.example__slider {
    margin: 0 auto;
    margin-top: 35px;
    @media (min-width: $tablet-width) {
        margin: 0;
    }
}