.statistic__item {
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 124px;
    height: 56px;
    border: 1px solid $special-grey;
    border-radius: 3px;
    @media (min-width: $tablet-width) {
        width: 162px;
        height: 72px;
    }
    @media (min-width: $desktop-width) {
        margin: 0;
    }
}


.statistic__description {
    font-family: $arial;
    font-style: normal;
    font-weight: normal;
    text-transform: none;
    font-size: 12px;
    line-height: 12px;
    color: $grey;
    width: 87px;
    margin: 0 auto;
    position: relative;
    bottom: -10px;
    background-color: $grey-ea;
    @media (min-width: $tablet-width) {
        font-size: 14px;
        line-height: 14px;
        width: auto;
        bottom: -5px;
        padding: 5px 8px 7px 8px;
        background-color: $white;
        & br {
            display: none;
        }
    }
    @media (min-width: $desktop-width) {
        background-color: $grey-f2;
    }
}

.statistic__number {
    font-size: 24px;
    line-height: 24px;
    margin: 0 auto;
    margin-top: 14px;
    @media (min-width: $tablet-width) {
        font-size: 30px;
        line-height: 37px;
    }
}
