.page-header {
    display: flex;
    flex-direction: column;
    width: 280px;
    margin: 0 auto;
    @media (min-width: $tablet-width) {
        flex-direction: row;
        justify-content: space-between;
        width: 709px;
        min-height: 52px;
        margin-top: 20px;
        padding: 0;
    }
    @media (min-width: $desktop-width) {
        width: 1220px;
        min-height: 59px;
        justify-content: space-between;
        margin-top: 51px;
        padding-top: 2px;
    }
}

.page-header__logo {
    display: block;
    max-width: 202px;
    height: auto;
    margin-top: 13px;
    margin-bottom: 16px;
    &:hover {
        opacity: 0.8;
    }
    &:active {
        opacity: 0.6;
    }
    & img {
        width: 190px;
        height: 38px;
    }
    @media (min-width: $tablet-width) {
        margin: 0;
        & img {
            width: 174px;
            height: 52px;
        }
    }
    @media (min-width: $desktop-width) {
        & img {
            width: 202px;
            height: 59px;
        }
    }
}