.page-title {
    font-style: normal;
    font-weight: normal;
    font-size: 36px;
    line-height: 36px;
    text-transform: none;
    color: $black;
    width: 280px;
    margin: 0 auto;
    &--catalog {
        padding-top: 27px;
        margin-bottom: 41px;
        position: relative;
    }
    &--program {
        padding-top: 27px;
        margin-bottom: 37px;
        position: relative;
    }
    @media (max-width: $mobile-width-only) {
        &::before {
            content: "";
            position: absolute;
            width: 200vw;
            top: 0;
            left: -40vw;
            height: 1px;
            background-color: $grey-menu;
        }
    }
    @media (min-width: $tablet-width) {
        font-size: 60px;
        line-height: 60px;
        width: 708px;
        margin-top: 65px;
        padding: 0;
        &--catalog {
            border: none;
            margin-bottom: 160px;
        }
        &--program {
            margin-top: 65px;
            margin-bottom: 54px;
        }
    }
    @media (min-width: $desktop-width) {
        width: 1220px;
        margin-top: 70px;
        &--catalog {
            margin-top: 71px;
        }
        &--program {
            margin-top: 70px;
            margin-bottom: 56px;
        }
    }
}