.main-nav {
    display: flex;
    flex-direction: column;
    min-height: 0;
    position: relative;
    &--closed .main-nav__toggle::before {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 24px;
        height: 16px;
        background-image: url("../img/icon-mobile-menu.svg");
        background-position: center;
        background-repeat: no-repeat;
    }
    &--opened .main-nav__toggle {
        width: 18px;
        height: 18px;
        &::before {
            content: "";
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 18px;
            height: 18px;
            background-image: url("../img/icon-close.svg");
            background-position: center;
            background-repeat: no-repeat;
        }
    }
    @media (max-width: $mobile-width-only) {
        &--closed {
            & .site-list__item {
                display: none;
            }
        }
        &--opened {
            min-height: 198px;
        }
        &--nojs {
            & .main-nav__toggle {
                display: none;
            }
            & .site-list__item {
                display: block;
            }
        }
    }
    @media (min-width: $tablet-width) {
        flex-direction: row;
        align-items: center;
        min-height: 0;
    }
}


.main-nav__list {
    list-style: none;
    margin: 0 auto;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    @media (min-width: $tablet-width) {
        flex-direction: row;
        justify-content: space-around;
        flex-wrap: wrap;
        padding-top: 5px;
    }
    @media (min-width: $desktop-width) {
        padding: 0;
        max-width: 610px;
    }
}

.main-nav__toggle {
    position: absolute;
    display: block;
    width: 24px;
    height: 16px;
    top: -41px;
    left: 259px;
    border: none;
    outline: none;
    background-color: $white;
    padding: 0;
    &:hover {
        opacity: 0.6;
    }
    &:active {
        opacity: 0.3;
    }
    @media (min-width: $tablet-width) {
        display: none;
    }
}
