.site-list__item {
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    position: relative;
    width: 100%;
    @media (max-width: $mobile-width-only) {
        &::before {
            position: absolute;
            content: "";
            top: 0;
            left: -40vw;
            width: 1000vw;
            height: 1px;
            background-color: $grey-menu;
        }
    }
    @media (min-width: $tablet-width) {
        font-weight: 400;
        width: auto;
        font-size: 18px;
        line-height: 24px;
        margin-right: 34px;
        &:last-child {
            margin-right: 0;
        }
        &--active {
            position: relative;
            &::after {
                position: absolute;
                content: "";
                width: 100%;
                height: 2px;
                background-color: $green;
                bottom: -7px;
            }
        }
    }
    @media (min-width: $desktop-width) {
        font-size: 20px;
        line-height: 30px;
        &--active-index {
            &::after {
                background-color: $white;
            }
        }
    }
}

.site-list__link {
    display: block;
    text-decoration: none;
    color: $black;
    padding-top: 22px;
    padding-bottom: 24px;
    text-align: center;
    &:hover {
        opacity: 0.6;
    }
    &:active {
        opacity: 0.3;
    }
    @media (min-width: $tablet-width) {
        padding: 0;
    }
    @media (min-width: $desktop-width) {
        &--index {
            color: $white;
        }
    }
}