.extra__headline {
  font-weight:normal;
  text-transform:none;
    font-size: 24px;
    line-height: 24px;
    width: 280px;
    margin: 0 auto;
    margin-bottom: 35px;
    margin-top: 28px;
    position: relative;
    &::after {
        position: absolute;
        content: "";
        width: 100vw;
        height: 2px;
        left: 248px;
        top: 50%;
        background-color: $green;
    }
    @media (min-width: $tablet-width) {
        font-size: 36px;
        line-height: 36px;
        width: 708px;
        margin-top: 68px;
        margin-bottom: 80px;
        padding-left: 37px;
        &::after {
            left: 411px;
            top: 20px;
        }
        &::before {
            position: absolute;
            content: "";
            width: 100vw;
            height: 2px;
            left: -100vw;
            top: 20px;
            background-color: $green;
        }
    }
    @media (min-width: $desktop-width) {
        width: 1220px;
        margin-top: 74px;
        margin-bottom: 80px;
        padding: 0;
        &::before {
            left: calc(-100vw - 40px);
        }
        &::after {
            left: 373px;
        }
    }
}

.extra__wrap {
    @media (min-width: $desktop-width) {
        display: flex;
        justify-content: space-between;
        width: 1220px;
        margin: 0 auto;
        margin-bottom: 86px;
    }
}

.extra__wrapper {
    width: 280px;
    margin: 0 auto;
    @media (min-width: $tablet-width) {
        width: 708px;
        border: 1px solid $special-grey;
    }
    @media (min-width: $desktop-width) {
        width: 895px;
        border: none;
        margin: 0;
    }
}

.extra__list {
    list-style: none;
    margin: 0 auto;
    padding: 0;
    display: flex;
    flex-direction: column;
}

.extra__item {
    display: flex;
    flex-direction: column;
    padding-top: 13px;
    padding-bottom: 18px;
    border-bottom: 1px solid $special-grey;
    &:first-child {
        border-top: 1px solid $special-grey;
    }
    @media (min-width: $tablet-width) {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        padding-left: 38px;
        padding-right: 38px;
        padding-bottom: 28px;
        padding-top: 24px;
        &:last-child {
            border: none;
        }
    }
    @media (min-width: $desktop-width) {
        justify-content: start;
        padding: 0;
        padding-top: 15px;
        padding-bottom: 15px;
        &:first-child {
            border-top: 1px solid $special-grey;
        }
        &:last-child {
            border-bottom: 1px solid $special-grey;
        }
    }
}

.extra__item-headline {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 16px;
    color: $dark-grey;
    margin: 0;
    padding: 0;
    margin-bottom: 13px;
    @media (min-width: $tablet-width) {
        font-size: 20px;
        line-height: 20px;
        margin-bottom: 25px;
    }
    @media (min-width: $desktop-width) {
        margin: 0;
        width: 325px;
    }
}

.extra-table__list {
    display: flex;
    justify-content: space-between;
    padding: 0;
    margin: 0;
    margin-bottom: 10px;
    @media (min-width: $tablet-width) {
        margin-bottom: 20px;
    }
    @media (min-width: $desktop-width) {
        margin: 0;
        width: 286px;
        margin-right: 36px;
        padding-top: 6px;
    }
}

.extra-table__item {
    font-family: $arial;
    font-style: normal;
    font-weight: normal;
    text-transform: none;
    font-size: 14px;
    line-height: 14px;
    padding: 0;
    margin: 0;
    color: $grey;
    @media (min-width: $tablet-width) {
        font-size: 16px;
        line-height: 20px;
        margin-right: 51px;
        &:last-child {
            margin-right: 0;
        }
    }
    @media (min-width: $desktop-width) {
        margin: 0;
    }
}

.extra__button {
    display: block;
    line-height: 20px;
    padding-top: 10px;
    text-decoration: none;
    &:hover,
    &:focus {
        background-color: $green-hover;
    }
    &:active {
        color: rgba(255, 255, 255, 0.3);
    }
    @media (min-width: $tablet-width) {
        width: 632px;
    }
    @media (min-width: $desktop-width) {
        width: 244px;
    }
}
