 .promo {
     display: flex;
     flex-direction: column;
     flex-wrap: wrap;
     align-items: center;
     margin: 0 auto;
     position: relative;
     @media (max-width: $mobile-width-only) {
         position: relative;
         &::before {
             position: absolute;
             content: "";
             z-index: -1;
             width: 100vw;
             height: 305px;
             top: 0;
             background: $background url("../img/index-background-mobile@1x.jpg");
             background-repeat: no-repeat;
             background-position: center top;
             background-size: auto;
         }
         @media (min-resolution: 2dppx) {
             &::before {
                 background: $background url("../img/index-background-mobile@2x.jpg");
                 background-repeat: no-repeat;
                 background-position: center top;
                 background-size: 320px auto;
             }
         }
     }
     @media (min-width: $tablet-width) {
         flex-direction: column;
         align-items: flex-start;
         text-align: start;
         width: 709px;
         min-height: 872px;
         padding-left: 87px;
     }
     @media (min-width: $desktop-width) {
         flex-direction: column;
         flex-wrap: nowrap;
         width: 1220px;
         padding-left: 80px;
         min-height: 584px;
         margin-bottom: 76px;
         padding-left: 160px;
         position: relative;
     }
 }
 
 .promo__headline {
     font-style: normal;
     font-weight: normal;
     text-transform: none;
     font-size: 36px;
     line-height: 36px;
     text-align: center;
     color: $white;
     width: 249px;
     margin-top: 27px;
     margin-bottom: 25px;
     @media (min-width: $tablet-width) {
         font-size: 60px;
         line-height: 60px;
         text-align: left;
         color: $black;
         width: 414px;
         margin-top: 64px;
         margin-bottom: 41px;
     }
     @media (min-width: $desktop-width) {
         margin-bottom: 40px;
         margin-top: 106px;
         position: relative;
         &::before {
             position: absolute;
             content: "";
             width: 50vw;
             height: 696px;
             background: $green url("../img/index-background-desktop@1x.jpg");
             background-position: center;
             background-repeat: no-repeat;
             background-size: cover;
             z-index: -1;
             top: -226px;
             left: 530px;
         }
         @media (min-resolution: 2dppx) {
             &::before {
                 background: $background url("../img/index-background-desktop@2x.jpg");
                 background-position: center;
                 background-repeat: no-repeat;
                 background-size: 720px auto;
             }
         }
     }
 }
 
 .promo__slogan {
     font-size: 14px;
     line-height: 14px;
     text-transform: uppercase;
     color: $white;
     margin: 0;
     margin-bottom: 29px;
     @media (min-width: $tablet-width) {
         font-size: 20px;
         line-height: 20px;
         color: $black;
         margin-bottom: 49px;
     }
     @media (min-width: $desktop-width) {
         margin-bottom: 52px;
     }
 }
 
 .promo__pic {
     width: 280px;
     height: 270px;
     margin-bottom: 3px;
     @media (min-width: $tablet-width) {
         width: 709px;
         height: 609px;
         margin: 0;
         order: 1;
         position: absolute;
         top: 263px;
         left: 0;
     }
     @media (min-width: $desktop-width) {
         width: 552px;
         height: 499px;
         padding: 0;
         order: 1;
         position: absolute;
         top: 26px;
         left: 528px;
     }
 }
 
 .promo__button {
     display: block;
     font-size: 14px;
     line-height: 18px;
     list-style: none;
     text-decoration: none;
     padding-top: 11px;
     margin: 0;
     &:hover {
         background-color: $green-hover;
     }
     &:active {
         color: rgba(255, 255, 255, 0.3);
     }
     @media (min-width: $tablet-width) {
         font-size: 20px;
         line-height: 26px;
         width: 244px;
         height: 54px;
         z-index: 2;
         order: 2;
         padding-top: 14px;
         position: relative;
         left: -5px;
     }
     @media (min-width: $desktop-width) {
         left: 0;
     }
 }